.container {
  position: absolute;
  inset: 0;
}

.toolbar {
  background-color: var(--background-color);
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
  position: sticky;
  z-index: 1000000;
  top: 0;
  padding: 1em;
  display: grid;
}

.toolbar > * {
  grid-column-start: 1;
  grid-row-start: 1;
  justify-self: center;
  display: inline-block;
}

.title {
  font-size: 1.9em;
}

.ws_conn {
  justify-self: right;
}